
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.primary-btn{
  padding: 8px 8px;
  background: #21CE90;
  color:#FFFFFF;
  border-width: 0px; /* 边框宽度 */
	border-radius: 3px; /* 边框半径 */
	cursor: pointer; /* 鼠标移入按钮范围时出现手势 */
	outline: none; /* 不显示轮廓线 */
}

.codesection[data-v-7fdf4a4b]{
  margin: 10px 10px;
  /* padding: 8px 8px; */
  background: #e3e3e3;
  border:1px solid #e3e3e3;
  border-radius: 10px;
  font-size: 14px;
  text-align: left;
}
.title[data-v-7fdf4a4b]{
  padding: 8px 8px;
  font-size: 16px;
  font-weight: 500;
  background: #999999;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.code[data-v-7fdf4a4b]{
  overflow: auto;
  padding: 8px 8px;
}
.result[data-v-7fdf4a4b]{
  padding: 8px 8px;
  border-top: 1px solid #999999;
  overflow: auto;
}
.actions[data-v-7fdf4a4b]{
  padding: 8px 8px;
  border-top: 1px solid #999999;
}
